:root {
  --light-border: #eeeeee;
  --jetpack-green: #069e08;
  --primary-black: #23282d;
  --primary-white: #ffffff;
  --primary-grey: #444444;
  --secondary-grey: #888888;
  --color_warning: hsl(28deg 94% 68%);
  --alt_white: #f9f9f6;
  --gray_0: #f6f7f7;
  --gray_5: #dcdcde;
  --gray_10: #c3c4c7;
  --gray_20: #a7aaad;
  --gray_30: #8c8f94;
  --gray_40: #787c82;
  --gray_50: #646970;
  --gray_60: #50575e;
  --gray_70: #3c434a;
  --gray_80: #2c3338;
  --gray_90: #1d2327;
  --gray_100: #101517;
  --gray: $gray_50;
  --jetpack_green_0: #f0f2eb;
  --jetpack_green_5: #d0e6b8;
  --jetpack_green_10: #9dd977;
  --jetpack_green_20: #64ca43;
  --jetpack_green_30: #2fb41f;
  --jetpack_green_40: #069e08;
  --jetpack_green_50: #008710;
  --jetpack_green_60: #007117;
  --jetpack_green_70: #005b18;
  --jetpack_green_80: #004515;
  --jetpack_green_90: #003010;
  --jetpack_green_100: #001c09;
  --red_50: #d63638;
  --orange_20: #faa754;
  --jetpack_green: $jetpack_green_50;
}

#jetpack-inspect * {
  box-sizing: border-box;
}

.jetpack-inspect {
  --wp-admin-theme-color: var(--jetpack_green_50);
  --wp-admin-theme-link-color: var(--gray_90);
  --wp-admin-theme-color-darker-10: var(--jetpack_green_60);
  --wp-admin-theme-color-darker-20: var(--jetpack_green_70);
}
.jetpack-inspect a {
  color: var(--wp-admin-theme-link-color);
}
.jetpack-inspect .components-button.is-primary {
  background-color: var(--jetpack_green);
  border-radius: var(--border-radius);
  font-weight: 600;
  margin-bottom: 1.5rem;
}
.jetpack-inspect .components-button.is-link {
  color: var(--wp-admin-theme-link-color);
}
.jetpack-inspect h1,
.jetpack-inspect h2,
.jetpack-inspect h3,
.jetpack-inspect h4,
.jetpack-inspect h5,
.jetpack-inspect h6 {
  margin: 0;
  color: var(--primary-black);
  line-height: 1.3;
  font-weight: 500;
}
.jetpack-inspect h2 {
  font-size: 36px;
  line-height: 1.1;
}
.jetpack-inspect h3 {
  font-size: 24px;
}
.jetpack-inspect p {
  color: var(--gray_80);
}
.jetpack-inspect button.secondary,
.jetpack-inspect a.button-secondary {
  color: var(--primary-black);
  background: none;
  border: 1px solid var(--primary-black);
  padding: 8px 24px;
  border-radius: 3px;
  font-weight: 600;
  cursor: pointer;
  font-size: 16px;
  line-height: 24px;
}
.jetpack-inspect button.secondary:hover,
.jetpack-inspect a.button-secondary:hover {
  background: var(--gray_0);
  border-color: var(--primary-black);
  color: var(--primary-black);
}

.jetpack_page_jetpack-inspect #wpcontent {
  padding-left: 0;
  min-height: 100vh;
}

.info code {
  background-color: hsl(0deg, 0%, 92%);
  border-radius: 3px;
}

.info code {
  font-size: 11px;
}

.button-effects, .ji-button, .ji-button--altii, .ji-button--alt {
  transition: transform 200ms ease;
}
.button-effects:hover, .ji-button:hover, .ji-button--altii:hover, .ji-button--alt:hover {
  opacity: 0.8;
}
.button-effects:active, .ji-button:active, .ji-button--altii:active, .ji-button--alt:active {
  outline: none;
  transform: translateY(1px);
}

.ji-button, .ji-button--altii, .ji-button--alt {
  display: inline-block;
  padding: 9px 21px;
  font-size: 0.75rem;
  background-color: var(--jetpack_green_40);
  color: var(--jetpack_green_0);
  border-radius: 21px;
  border: none;
  font-weight: 400;
  cursor: pointer;
}

.ji-button--alt {
  background-color: hsl(77deg, 43%, 90%);
  color: var(--jetpack_green_40);
}

.ji-button--altii {
  background-color: hsl(77deg, 43%, 90%);
  color: var(--jetpack_green_40);
}

#jetpack-inspect form :focus,
#jetpack-inspect textarea:focus,
#jetpack-inspect input:focus {
  outline-color: var(--jetpack_green_40);
}
#jetpack-inspect input,
#jetpack-inspect textarea {
  border: 1px solid var(--gray_5);
  border-radius: 3px;
}
#jetpack-inspect input[type=text] {
  padding-left: 10px;
}
#jetpack-inspect input[type=text]::placeholder {
  opacity: 0.3;
}